import React, {Component} from 'react'
import {Toast} from 'antd-mobile'
import './index.scss'
class Mine extends Component{
  state = {
    userPower: [
      {
        icon: 'icon-order',
        name: ''
      },
      {
        icon: 'icon-rili',
        name: ''
      },
      {
        icon: 'icon-iconfontdizhi',
        name: ''
      },
      {
        icon: 'icon-shoucang',
        name: ''
      },
      {
        icon: 'icon-youhuiquan',
        name: ''
      },
      {
        icon: 'icon-youxuan',
        name: ''
      },
      {
        icon: 'icon-hongbao',
        name: ''
      },
      {
        icon: 'icon-huiyuan',
        name: 'plus'
      },
      {
        icon: 'icon-yaoqing',
        name: ''
      },
      {
        icon: 'icon-yijianfankui',
        name: ''
      },
      {
        icon: 'icon-kefu',
        name: ''
      },
      {
        icon: 'icon-baohu',
        name: ''
      }
    ]
  }
  showPower (item) {
    Toast.offline(`${item.name}~`,1)
  }
  render () {
    const userPhone = window.localStorage.getItem('nideShopUser')
    return (
      <div id="minePage">
        <div className="userMsgWrap">
          <div className="userLogo"></div>
          {
            userPhone?
              <div className="userMsgs">
                <div>{userPhone}</div>
                <div></div>
              </div>:
              <div className="userMsgs">
                <div style={{height: '100%'}}></div>
              </div>
          }
        </div>
        <div className="userPower">
          {
            this.state.userPower.map((item) => (
              <div key={item.name} onClick={this.showPower.bind(this,item)}>
                <i className={`iconfont ${item.icon}`}></i>
                <div>{item.name}</div>
              </div>)
            )
          }
        </div>
      </div>
    )
  }
}
export default Mine